body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  width: 100%;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.intro {
  background-image: url(/Portfolio/static/media/blue-background.e88312cd.jpg);
  background-size: cover;
  color: #DCAE1D;
  height: 400px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.projectSection {
  background: #87d0e2;
  min-height: 400px;
  text-align: center;
  padding-bottom: 20px;
}

.projectTitle {
  color: #DCAE1D;
}

.projects {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 200px 0 200px;
}

@media(max-width: 1000px){
  .projects {
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 0;
  }
}

.contact {
  background: rgb(2, 17, 61);
  height: 220px;
  color: #DCAE1D;
  text-align: center;
}

.contactIcons {
  padding: 30px 500px 0 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

@media(max-width: 1240px){
  .contactIcons {
    padding: 0 200px 0 200px;
  }
}

@media(max-width: 700px){
  .contactIcons {
    padding: 0 100px 0 100px;
  }
}

@media(max-width: 450px){
  .contactIcons {
    padding: 0 50px 0 50px;
  }
}

.myPicture {
  max-width: 15%;
  min-width: 200px;
  border-radius: 50%;
}

.fa-linkedin {
  color: #DCAE1D;
  font-size: 50px;
}
.fa-linkedin:hover {
  color: rgb(255, 147, 47);
}

.fa-envelope {
  color: #DCAE1D;
  font-size: 50px;
}
.fa-envelope:hover {
  color: rgb(255, 147, 47);
}

.fa-github {
  color: #DCAE1D;
  font-size: 50px;
}
.fa-github:hover {
  color: rgb(255, 147, 47);
}
.projectCard {
    height: 300px;
    width: 300px;
    border: 1px solid rgb(11, 41, 124);
    border-radius: 10px;
    margin: 15px 0 15px 0;
    background: #DCAE1D;
    box-shadow: 0 5px 20px rgb(11, 41, 124), 0 6px 6px rgb(11, 41, 124);
    text-decoration: none;
}

.projectCard:hover {
    background: rgb(255, 147, 47);
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    cursor: pointer;
}

.description {
    padding: 0 10px 0 15px;
    color: black;
}

.projectImage {
    width: 100%;
    height: 200px;
    border-radius: 9px 9px 0 0;
}

.icon:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    cursor: pointer;
}
